.cloudContainer h2 {
    margin: 0px auto 50px;
    font-size: 3rem;
    color: #2364BC;
    letter-spacing: .3rem;
}
.cloudContainer h2 > span {
    font-size: 1.5rem;
    display: block;
}

.cloudContainer h3 {
    font-size: 2rem;
    margin: 0px auto 30px;
}

.cloudContainer > div:not(#lnb, .footerContainer) {
    margin: 100px 0px;
    padding: 50px 0px;
}

.cloudContainer > .cloudInfo > p {
    font-size: 1.3rem;
    line-height: 2rem;
}

.cloudContainer > div > p {
    font-size: 1.5rem;
    line-height: 3rem;
    width: 50%;
    margin: 0 auto;
    text-align: center;
}
