@font-face {
    font-family: 'Noto Sans KR';
    src: url("./fonts/NotoSansKR-Regular.otf");
  }

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Noto Sans KR';
    color: #333;
    text-align: center;
}

a {
    text-decoration: none;
    cursor: pointer;
}

ol,ul {
    list-style: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

::selection {
    background-color: #111;
    color: #fff;
}

.cursor {
    width: 30px;
    height: 30px;
    margin: -15px;
    background-color: aliceblue;
    position: fixed;
    border-radius: 50%;
    z-index: 99999;
    pointer-events: none;
    mix-blend-mode: difference;
}