header {
  width: 100%;
  height: 80px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.3);
  padding: 15px 100px;
  box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.2);
  transition: background-color .2s;
  z-index: 9999;
}

h1 > a {
  display: inline-block;
  width: 200px;
  height: 50px;
  background: url('./images/logo-W.png') no-repeat center / contain;
}

nav {
  width: calc(100% - 350px);
}

#gnb {
  display: flex;
  justify-content: center;
  height: 50px;
}

#gnb li {
  margin:  0px 30px;
  line-height: 50px;
}

nav a {
  color: #efefef;
  font-size: 1.2rem;
  /* transition: all .2s; */
  display: inline-block;
}

nav a > .underBar {
  height: 3px;
  width: 0%;
  margin: -5%;
  background-color: rgb(0, 28, 119);
}

header:hover {
  background-color: #fff;
}

header:hover h1 > a {
  background: url('./images/logo-B.png') no-repeat center / contain;
}

header:hover nav a {
  color: #333;
}

header:hover nav a:hover .underBar {
  animation: line .2s forwards;
}

@keyframes line {
  from {width: 0;}
  to {width: 110%;}
}