.vivaContainer > div:not(#lnb, .footerContainer) {
    margin: 100px 0px;
    padding: 50px 0px;
}

.vivaContainer > div:nth-child(3) {
    background-color: #F1F7FF;
}

.vivaContainer h2 {
    margin: 0px auto 50px;
    font-size: 3rem;
    color: #2364BC;
    letter-spacing: .3rem;
}

.vivaContainer > div > p {
    font-size: 1.5rem;
    line-height: 3rem;
}

.subvalue > img {
    max-width: 700px;
    margin: 50px auto;
}