.conTop {
    width: 100%;
    height: 300px;
    line-height: 300px;
    font-weight: bold;
    font-size: 2.5rem;
    color: #fff;
    text-shadow: rgba(0,0,0,0.3) 1px 1px 5px;
    background: url('../images/busTop.png') no-repeat center / cover;
}

.topBtn {
    width: 50px;
    height: 50px;
    background: url("../images/top.png");
    position: fixed;
    right: 50px;
    bottom: 50px;
    box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.3);
    cursor: pointer;
    opacity: 0;
    transition: opacity .2s;
}

.topBtn.on {
    opacity: 1;
}

.conContents h2 {
    margin: 0px auto 50px;
    font-size: 3rem;
    color: #2364BC;
}

.conContents > div {
    margin: 100px 0px;
    padding: 50px 0px;
}

.location > div {
    width: 50%;
    margin: 0 auto;
}

.location > div > div {
    text-align: left;
    margin: 20px 0px;
    height: 30px;
    font-size: 1.1rem;
    display: flex;
}

.location > div > div p {
    line-height: 30px;
    margin-left: 10px;
}

form {
    display: flex;
    justify-content: space-evenly;
    width: 50%;
    flex-wrap: wrap;
    margin: 0 auto;
}

form > div:first-child, form > div:nth-child(2),form > div:nth-child(3) {
    width: 100%;
    margin: 20px 0px;
}

form input[type=text],form input[type=email] {
    padding: 10px;
    font-size: 1.2rem;
    border: none;
    border-radius: 5px;
    background-color: #eee;
    width: 100%;
    font-family: 'Noto Sans KR';
}

input:focus, textarea:focus {outline:none;}

form textarea {
    padding: 10px;
    font-size: 1.2rem;
    width: 100%;
    min-height: 150px;
    resize: none;
    border: none;
    border-radius: 5px;
    background-color: #eee;
    font-family: 'Noto Sans KR';
    color: #333;
}

form input[type=submit] {
    width: 150px;
    height: 50px;
    font-size: 1.5rem;
    border: none;
    font-weight: bold;
    background: url('../images/send.png');
    cursor: pointer;
    margin-top: 50px;
    color: #333;
    transition: .2s;
}

form input[type=submit]:hover {
    color: #2364BC;
    background: url('../images/sendhover.png');
}