/* ---------------- fullpage ---------------- */

.section {
    height: 100vh;
}

.section.first h2 {
    color: #fff;
    font-size: 5rem;
    position: absolute;
    top: 35%;
    right: 10%;
}

.section.second > div {
    background: url('../images/mainBg2.png') no-repeat top / cover;
}

.section.third > div {
    background: url('../images/mainBg3.jpg') no-repeat center / cover;
}

.section.forth > div {
    background-color: #F1F7FF;
}

#fp-nav ul li a span, .fp-slidesNav ul li a span {
    background-color: rgba(58, 87, 255, 0.4) !important;
}

/* ---------------- slides ---------------- */

.slide-container, .slide-container div {
    height: 100%;
}

.each-fade > div img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.react-slideshow-container .default-nav {
    display: none !important;
}

.section h2 {
    font-size: 5rem;
    letter-spacing: 0.5rem;
    text-align: center;
    position: relative;
    top: 180px;
}

.section h2 > span {
    font-size: 1.5rem;
    letter-spacing: 2rem;
    display: block;
    font-weight: normal;
}


/* ---------------- section 2 ---------------- */
.section > div > div {
    width: 100%;
    height: 100%;
    /* position: relative; */
}

.second h2 {
    color: #fff;
}

.second .contents {
    position: absolute;
    bottom: 0;
    display: flex;
    color: #fff;
    width: 100%;
    height: 60%;
    justify-content: center;
    text-align: left;
}

.second .contents > div {
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 50px 50px 80px;
    /* border: 1px solid red; */
}

.second .contents > div > * {
    margin: 10px 0px;
}

.second .contents > div > p:first-child {
    font-size: 2.5rem;
    font-weight: bold;
    letter-spacing: 0.5rem;
}

.bar {
    width: 0;
    height: 2px;
    background: -moz-linear-gradient(left,  rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=1 ); /* IE6-9 */
    transition: width .4s;
}

.second .contents > div > p:nth-child(3) {
    font-size: 1.4rem;
    line-height: 2.5rem;
}

.second .contents > div > p:nth-child(3), .second .contents > div > a:nth-child(4) {
    color: #fff;
    opacity: 0;
    height: 0;
    transition: all .2s;
}

.second .contents > div:hover {
    background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,0.65) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=0 ); /* IE6-9 */
}

.second .contents > div:hover > p:first-child {
    color: #AACD06;
}

.second .contents > div:hover .bar {
    width: 100%;
}

.second .contents > div:hover > p:nth-child(3), .second .contents > div:hover > a:nth-child(4) {
    opacity: 1;
    pointer-events: auto;
    height: unset;
}

.second .contents > div:hover > p:nth-child(3) {
    margin: 40px 0px;
}

.second .contents > div:hover > a:nth-child(4) > img {
    display: inline-block;
    height: 40%;
    margin-left: 10px;
}

.second .contents > div:hover > a:nth-child(4) > img {
    animation: arrow .5s infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
}

@keyframes arrow {
    from {margin-left: 10px;}
    to {margin-left: 20px;}
}

.vision, .value {
    border-right: 2px solid rgba(255, 255, 255, 0.3);
}


/* ---------------- section 3 ---------------- */
.third .contents {
    width: 100%;
    position: relative;
    top: 32%;
    display: flex;
    justify-content: center;
}

.third .contents > div {
    width: 25%;
}

.third .contents > div > div:first-child {
    /* border: 1px solid red; */
    box-shadow: 1px 1px 10px 0px rgba(0,0,0, 0.3);
    width: 90%;
    min-width: 350px;
    min-height: 350px;
    margin: 0px auto 10px;
    padding: 40px;
    position: relative;
    top: 20px;
    transition: top .3s;
    overflow: hidden;
    cursor: pointer;
}

.third .contents > div > div:first-child > p {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 10px;
}

.third .contents > div > div:first-child > p > span {
    display: block;
    font-size: 1rem;
    font-weight: normal;
}

.third .contents > div > div:first-child > a {
    position: absolute;
    bottom: 40px;
    left: -100px;
    transition: left .3s;
}

.third .contents > div > div:first-child > a > img {
    width: 90px;
}

.third .contents > div {
    text-align: left;
}

.third .contents > div > div:last-child {
    width: 0%;
    overflow: hidden;
    float: right;
    transition: width .5s;
}

.third .contents > div > div:last-child > p {
    margin: 0 auto;
    min-width: 350px;
    font-size: 1.1rem;
    border-left: 10px solid #2364BC;
    padding-left: 15px;
    width: 90%;
    opacity: 0;
}

.third .contents > div:hover > div:first-child {
    top: -20px;
}

.third .contents > div:hover > div:first-child > a {
    left: 40px;
}

.third .contents > div:hover > div:last-child {
    width: 100%;
}

.third .contents > div:hover > div:last-child > p {
    opacity: 1;
}

.deeplearning > div:first-child {
    background: url('../images/deeplearning-gray.png') no-repeat center / cover;
    transition: all .2s;
}
.deeplearning:hover > div:first-child {
    background: url('../images/deeplearning.png') no-repeat center / cover;
    color: #fff;
}

.bigdata > div:first-child {
    background: url('../images/bigdata-gray.png') no-repeat center / cover;
    transition: all .2s;
}
.bigdata:hover > div:first-child {
    background: url('../images/bigdata.png') no-repeat center / cover;
    color: #fff;
}

.cloud > div:first-child {
    background: url('../images/cloud-gray.png') no-repeat center / cover;
    transition: all .2s;
}
.cloud:hover > div:first-child {
    background: url('../images/cloud.png') no-repeat center / cover;
    color: #062857;
}


/* ---------------- section 4 ---------------- */
.forth .contents {
    width: 100%;
    height: 55%;
    display: flex;
    justify-content: center;
    position: relative;
    top: 20%;
}

.forth .contents > div {
    width: 20%;
    height: 100%;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    border-left: 1px solid #F1F7FF;
    border-right: 1px solid #F1F7FF;
}

.forth .contents > div > .bg {
    width: 100%;
    height: 100%;
    transition: all .3s;
}

.forth .contents > .m2m > .bg {
    background: url('../images/m2m.png') no-repeat center / cover;
    /* border-left: 2px solid #F1F7FF; */
}

.forth .contents > .installEx > .bg {
    background: url('../images/example.jpg') no-repeat center / cover;
    /* border-left: 2px solid #F1F7FF; */
}

.forth .contents > .product > .bg {
    background: url('../images/product.png') no-repeat center / cover;
    /* border-left: 2px solid #F1F7FF; */
}

.forth .contents > div:hover > .bg {
    transform: scale(1.05);
}

.forth .contents > div > .linkBox {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    font-size: 2.5rem;
    font-weight: bold;
    padding: 45% 0 0 10%;
    text-align: left;
    background-color: rgba(0,0,0, 0.3);
    opacity: 0;
    transition: opacity .5s;
}

.forth .contents > div > .linkBox > span {
    font-size: 1rem;
    font-weight: normal;
    display: inline-block;
    margin-top: 100px;
}

.forth .contents > div > .linkBox > span > img {
    width: 50px;
    margin-left: 10px;
    animation: arrow .5s infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
}

.forth .contents > div:hover > .linkBox {
    opacity: 1;
} 


/* ---------------- section 5 ---------------- */
.fifth .contents {
    display: flex;
}

.fifth .contents > div {
    width: 50%;
    position: relative;
}

.fifth .contents > .footer-left {
    text-align: left;
    padding: 100px 100px 100px 13%;
    background-color: #031D3C;
    color: #fff;
}

.fifth .contents > .footer-right {
    padding: 100px 150px;
}

.fifth .contents > div > h2 {
    text-align: left;
    z-index: 1;
    margin: 20px 0px;
    top: 130px;
}

.fifth .contents > .footer-left .circle1 {
    width: 100px;
    height: 100px;
    background-color: #AACD06;
    border-radius: 50%;
    position: absolute;
    left: 250px;
    top: 230px;
    z-index: 0;
    animation: circleUp 1s infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
}

/* .fifth .contents > .footer-left .circle2 {
    width: 75px;
    height: 75px;
    border: 3px solid #2364BC;
    border-radius: 50%;
    position: absolute;
    top: 300px;
    left: 700px;
    animation: circleScale .8s infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
} */

.fifth .contents > .footer-right .circle1 {
    width: 100px;
    height: 100px;
    border: 3px solid #AACD06;
    border-radius: 50%;
    position: absolute;
    top: 290px;
    left: 550px;
    animation: circleScale .8s infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
}

@keyframes circleUp {
    from {transform: translateY(0);}
    to {transform: translateY(10px);}
}

@keyframes circleScale {
    from {transform: scale(1);}
    to {transform: scale(1.1);}
}

.fifth .contents > .footer-left > .footerCon {
    position: relative;
    top: 20%;
    height: 40%;
}

.footerCon h3 {
    font-size: 2rem;
    margin: 20px 0px;
}

.footerCon > div {
    display: flex;
    flex-direction: column;
    height: 50%;
    justify-content: space-evenly;
    font-size: 1.2rem;
}

.footerCon > div p {
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    width: 80%;
}

.footerCon > div p > span {
    font-weight: normal;
    display: inline-block;
    width: 80%;
}

.footerCon > p:last-child {
    color: #ccc;
    font-size: .9rem;
    margin-top: 100px;
}

.fifth .contents > .footer-right {
    background-color: #eee;
}

.fifth .contents > .footer-right > h2 {
    color: #031D3C;
}
 