.footerContainer {
    width: 100%;
    height: 200px;
    background-color: #031d3b;
    display: flex;
    justify-content: center;
}

.footerContainer > div:first-child {
    width: 15%;
}

.footerContainer > div:first-child {
    margin: auto 0px;
}

.footerContainer > div:last-child {
    width: 45%;
    text-align: left;
    padding-left: 100px;
    color: #fff;
    border-left: 1px solid #fff;
}

.footerContainer > div:last-child > p:first-child {
    font-size: 1.3rem;
    margin: 25px 0px;
}

.footerContainer > div:last-child > .address {
    color: #ccc;
    margin: 20px 0px;
}

.footerContainer > div:last-child > .copy {
    color: #ddd;
    font-size: .8rem;
    margin: 25px 0px;
}